import React from 'react';
import { securityCompanies } from '../util';

function Securities({ onChange, setBuy, setSell, value }) {
  return (
    <div className="form-group">
      <label>Pengaturan Fee</label>
      <select
        className="form-control"
        onChange={e => {
          const { value } = e.target;

          const security = securityCompanies.find(
            security => security.name === value
          );

          onChange(security.name);
          setBuy(security.buyFee);
          setSell(security.sellFee);
        }}
        value={value}
      >
      {securityCompanies.map((security, i) => (
        <option key={i} value={security.name}>{security.name}</option>
      ))}
      </select>
      <small className="form-text text-muted">
        Atur fee berdasarkan sekuritas atau input manual.
      </small>
    </div>
  );
}

export default Securities;
