import React, { useState, useEffect } from 'react';
import NumberInput from './NumberInput';
import PercentageInput from './PercentageInput';
import { calculateRiskPerPositionInNumber } from '../util';

function RiskPerPosition({ onChange, totalCapital, value }) {
  const [
    riskPerPositionInNumber, setRiskPerPositionInNumber
  ] = useState(calculateRiskPerPositionInNumber(totalCapital || 0, value));

  useEffect(() => {
    setRiskPerPositionInNumber(
      calculateRiskPerPositionInNumber(totalCapital || 0, value)
    );
  }, [totalCapital, value]);

  return (
    <fieldset className="form-group">
      <legend className="col-form-label">Risiko Per Posisi</legend>
      <div className="form-row">
        <div className="form-group col-md-6 col-sm-6">
          <PercentageInput
            onChange={value => {
              onChange(value);

              setRiskPerPositionInNumber(
                calculateRiskPerPositionInNumber(totalCapital, value)
              );
            }}
            value={value}
          />
          <small className="form-text text-muted">
            Persentase risiko per posisi terhadap Total Modal.
          </small>
        </div>
        <div className="form-group col-md-6 col-sm-6">
          <NumberInput disabled value={riskPerPositionInNumber} />
          <small className="form-text text-muted">
            Nominal risiko per posisi.
          </small>
        </div>
      </div>
    </fieldset>
  );
}

export default RiskPerPosition;
