export const DEFAULT_VALUES_KEY = 'defaultValues';

export const INPUT_FEE_MANUAL = 'Input fee manual...';

export const LOT = 100;

export const defaultValues = {
  totalCapital: 100000000,
  riskPerPositionInPercentage: 1,
  securityCompany: INPUT_FEE_MANUAL,
  buyFeeInPercentage: 0.15,
  sellFeeInPercentage: 0.25,
  entryPrice: 1000,
  stopLossPrice: 950
};

export const saveDefaultValues = defaultValues => {
  localStorage.setItem(DEFAULT_VALUES_KEY, JSON.stringify(defaultValues));
};

export const initializeDefaultValues = () => {
  const values = localStorage.getItem(DEFAULT_VALUES_KEY);

  if (values === null) {
    saveDefaultValues(defaultValues);

    return defaultValues;
  }

  return JSON.parse(values);
};

export const calculateRiskPerPositionInNumber = (totalCapital, percentage) => (
  totalCapital * percentage / 100
);

export const calculateLoss = (
  lots,
  entryPrice,
  stopLossPrice,
  buyFee,
  sellFee
) => {
  const buy = entryPrice * lots * LOT;
  const buyWithFee = buy + (buy * buyFee / 100);

  const sell = stopLossPrice * lots * LOT;
  const sellWithFee = sell - (sell * sellFee / 100);

  return buyWithFee - sellWithFee;
};

export const calculateLots = (
  totalCapital,
  riskPerPositionInPercentage,
  entryPrice,
  stopLossPrice,
  buyFee,
  sellFee
) => {
  const buyInLot = entryPrice * LOT;
  const buyInLotWithFee = buyInLot + (entryPrice * buyFee);

  const sellInLot = stopLossPrice * LOT;
  const sellInLotWithFee = sellInLot - (stopLossPrice * sellFee);

  const riskPerPositionInNumber = calculateRiskPerPositionInNumber(
    totalCapital, riskPerPositionInPercentage
  );

  return Math.floor(
    riskPerPositionInNumber / (buyInLotWithFee - sellInLotWithFee)
  );
};

