import React, { useState } from 'react';
import EntryAndStopLoss from './EntryAndStopLoss';
import Fees from './Fees';
import Result from './Result';
import RiskPerPosition from './RiskPerPosition';
import Securities from './Securities';
import TotalCapital from './TotalCapital';
import analytics from '../analytics';
import {
  calculateLoss,
  calculateLots,
  INPUT_FEE_MANUAL,
  saveDefaultValues
} from '../util';

function Form ({ defaultValues }) {
  const [totalCapital, setTotalCapital] = useState(defaultValues.totalCapital);
  const [
    riskPerPositionInPercentage,
    setRiskPerPositionInPercentage
  ] = useState(defaultValues.riskPerPositionInPercentage);
  const [security, setSecurity] = useState(defaultValues.securityCompany);
  const [buyFee, setBuyFee] = useState(defaultValues.buyFeeInPercentage);
  const [sellFee, setSellFee] = useState(defaultValues.sellFeeInPercentage);
  const [entryPrice, setEntryPrice] = useState(defaultValues.entryPrice);
  const [
    stopLossPrice, setStopLossPrice
  ] = useState(defaultValues.stopLossPrice);
  const [loss, setLoss] = useState(0);
  const [lots, setLots] = useState(0);

  return (
    <form onSubmit={e => {
      e.preventDefault();

      if (stopLossPrice > entryPrice) {
        alert('Stop Loss tidak boleh lebih besar dari Entry!');
        return;
      }

      const lots = calculateLots(
        totalCapital,
        riskPerPositionInPercentage,
        entryPrice,
        stopLossPrice,
        buyFee,
        sellFee
      );

      setLots(lots);
      setLoss(calculateLoss(lots, entryPrice, stopLossPrice, buyFee, sellFee));

      analytics.track('calculate');

      saveDefaultValues({
        totalCapital,
        riskPerPositionInPercentage,
        securityCompany: security,
        buyFeeInPercentage: buyFee,
        sellFeeInPercentage: sellFee,
        entryPrice,
        stopLossPrice
      });
    }}>
      <TotalCapital onChange={setTotalCapital} value={totalCapital} />
      <RiskPerPosition
        onChange={setRiskPerPositionInPercentage}
        totalCapital={totalCapital}
        value={riskPerPositionInPercentage}
      />
      <Securities
        onChange={setSecurity}
        setBuy={setBuyFee}
        setSell={setSellFee}
        value={security}
      />
      <Fees
        buy={buyFee}
        onChangeBuy={setBuyFee}
        onChangeSell={setSellFee}
        sell={sellFee}
        disabled={security !== INPUT_FEE_MANUAL}
      />
      <EntryAndStopLoss
        entry={entryPrice}
        onChangeEntry={setEntryPrice}
        onChangeStopLoss={setStopLossPrice}
        stopLoss={stopLossPrice}
      />
      <div className="form-group">
        <button
          type="submit"
          className="btn btn-secondary btn-lg btn-block"
        >
          Hitung
        </button>
      </div>
      <Result loss={loss} lots={lots} />
    </form>
  );
}

export default Form;
